import React from 'react'
import JsUtil from 'tsx/common/JsUtil'
import URI from 'tsx/common/URI'
import ComponentView from 'tsx/view/ComponentView'
import GridLayout from 'tsx/layout/GridLayout'
import CommodityLayout from 'tsx/layout/CommodityLayout'
import SelectInput from 'tsx/form/SelectInput'

class CmmdListTemplate extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      sortingOptions: [{ value: 1, label: '按价格升序' }, { value: 3, label: '按价格降序' }, { value: 5, label: '按销量升序' }, { value: 7, label: '按销量降序' }],
      sorting: 1,
    }
  }

  sortingChanged = (item) => {
    const value = item.value;
    this.setState({ sorting: value })
    this.props.onSortChange(value);
  }

  go2CommodityDetail = (item, index) => {
    const commodityId = item.commodityId

    JsUtil.go2Page(URI.MALL_COMMODITY_DETAIL + '?commodityId=' + commodityId)
  }

  render() {
    const { sorting, sortingOptions } = this.state;
    const labelKey = 'label';
    const valueKey='value';
    const data = this.props.data;

    return (
      <ComponentView>
        <SelectInput options={sortingOptions} labelKey={labelKey} valueKey={valueKey} value={sorting} onChange={this.sortingChanged} label='排序' />
           <GridLayout data={data} columnSize={2}>
		   <CommodityLayout onClick={this.go2CommodityDetail} />
		   </GridLayout>
      </ComponentView>
    );
  }
}

export default CmmdListTemplate;
